// Since Swiper uses a shadow DOM in their web component, styling injection only works for inline styling. 

const SwiperStyles = `
    :host {
        --swiper-navigation-size: 18px;
        --swiper-navigation-color: vvar(--color-neutral-10);
    }

    .swiper-pagination {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 4rem;
        height: 4rem;
        left: unset;
        right: 4rem;
        bottom: 0;
        font-family: var(--font-family);
        font-weight: var(--font-weight-bold);
        background-color: var(--color-neutral-0);
    }

    .swiper-button-next.swiper-button-disabled, .swiper-button-prev.swiper-button-disabled {
        --swiper-navigation-color: transparent;

        opacity: unset;
    }

    .swiper-button-prev {
        top: unset;
        left: unset;
        bottom: 0;
        right: 8rem;
        width: 4rem;
        height: 4rem;
        background-color: var(--color-neutral-0);
    }

    .swiper-button-next {
        top: unset;
        left: unset;
        bottom: 0;
        right: 0;
        width: 4rem;
        height: 4rem;
        background-color: var(--color-neutral-0);
    }

    .swiper-pagination-current {
        margin-right: var(--spacing-xs);
    }

    .swiper-pagination-total {
        margin-left: var(--spacing-xs);
    }

    .gatsby-image-wrapper {
        height: 100%;
        width: 100%;
    }
`

export default SwiperStyles;